






































































































































































#treeVisualization {
  background: black;
  color: white;
  font-family: sans-serif;

  #guide {
    position: fixed;
    top: 5%;
    left: 5%;
    text-align: left;
    border: solid 2px #aaa;
    border-radius: 5px;
    padding: 10px;
  }

  svg {
    width: 100%;
    height: 100%;

    line.stem {
      z-index: 100;
    }
    line.link {
      z-index: 1;
    }

    #zoomContainer rect {
      width: 100vw;
      height: 100vh;
    }

    .agentData {
      p {
        margin: 0;
        padding: 0 10px;
        position: absolute;
        top: 50%;
        left: 50%;
        width: 100%;
        transform: translate(-50%, -50%);
        word-break: break-word;
      }
    }
  }
}
